.theme-overlay{
    position:relative
}
.theme-overlay::before{
    background:#1696e7 none repeat scroll 0 0;
    content:"";
    height:100%;
    left:0;
    opacity:0.6;
    position:absolute;
    top:0;
    width:100%
}
.overlay{
    position:relative;
    z-index:0
}
.overlay::before{
    position:absolute;
    content:"";
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:-1
}
.overlay2{
    position:relative;
    z-index:0
}
.overlay2::before{
    position:absolute;
    content:"";
    background-color:#2E2200;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:-1;
