.owl-carousel .owl-nav div{
    background:rgba(255,255,255,0.8) none repeat scroll 0 0;
    height:40px;
    left:20px;
    line-height:40px;
    font-size:22px;
    color:#646464;
    opacity:1;
    visibility:visible;
    position:absolute;
    text-align:center;
    top:50%;
    transform:translateY(-50%);
    transition:all 0.3s ease 0s;
    width:40px
}
.owl-carousel .owl-nav div.owl-next{
    left:auto;
    right:-30px
}
.owl-carousel .owl-nav div.owl-next i{
    position:relative;
    right:0;
    top:1px
}
.owl-carousel .owl-nav div.owl-prev i{
    position:relative;
    right:1px;
    top:0px
}
.owl-carousel:hover .owl-nav div{
    opacity:1;
    visibility:visible
}
.owl-carousel:hover .owl-nav div:hover{
    color:#fff;
    background:#ff3500
}
.btn{
    background:#ff2020;
    font-family:"Josefin Sans",sans-serif;
    text-transform:uppercase;
    color:#fff;
    font-size:16px;
    font-weight:600;
    letter-spacing:0.03em;
    padding:30px 28px;
    border-radius:0px;
    display:inline-block;
    line-height:0;
    cursor:pointer;
    margin-bottom:0;
    margin:10px;
    cursor:pointer;
    transition:color 0.4s linear;
    position:relative;
    z-index:1;
    -moz-user-select:none;
    border:0;
    overflow:hidden;
    margin:0
}
.btn::before{
    content:"";
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:#4a4a4b;
    z-index:1;
    transition:transform 0.5s;
    transition-timing-function:ease;
    transform-origin:0 0;
    transition-timing-function:cubic-bezier(0.5, 1.6, 0.4, 0.7);
    transform:scaleX(0);
    border-radius:0px
}
.btn:hover::before{
    transform:scaleX(1);
    color:#fff !important;
    z-index:-1
}
.header-btn{
    padding:25px 28px !important
}
@media (min-width: 1200px) and (max-width: 1400px){
    .header-btn{
        padding:25px 21px !important
    }
}
.btn.focus,.btn:focus{
    outline:0;
    box-shadow:none
}
