    padding:25px 29px;
    background:#4a4a4b
}
.hero-btn::before{
    background:#ff2020
}
.black-btn{
    background:#000;
    box-shadow:0px 20px 50px 0px rgba(22,22,22,0.2);
    transform:translateY(0px);
    cursor:pointer;
    display:inline-block;
    font-size:18px;
    font-weight:400;
    letter-spacing:1px;
    line-height:0;
    margin-bottom:0;
    padding:27px 44px;
    border-radius:25px
}
.black-btn:hover{
    box-shadow:0px 20px 50px 0px rgba(12,9,9,0.2);
    transform:translateY(-3px)
}
