.border-btn{
    background:none;
    -moz-user-select:none;
    border:2px solid #ff2020;
    padding:18px 38px;
    margin:10px;
    text-transform:capitalize;
    color:#ff2020;
    cursor:pointer;
    display:inline-block;
    font-size:14px;
    font-weight:500;
    letter-spacing:1px;
    margin-bottom:0;
    border-radius:5px;
    position:relative;
    transition:color 0.4s linear;
    position:relative;
    overflow:hidden;
    margin:0
}
.border-btn::before{
    border:2px solid transparent;
    content:"";
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:#ff2020;
    z-index:-1;
    transition:transform 0.5s;
    transition-timing-function:ease;
    transform-origin:0 0;
    transition-timing-function:cubic-bezier(0.5, 1.6, 0.4, 0.7);
    transform:scaleY(0)
}
.border-btn:hover::before{
    transform:scaleY(1);
    order:2px solid transparent
}
.border-btn.border-btn2{
    padding:17px 52px
}
.send-btn{
    background:#ff2020;
    color:#fff;
    font-size:14px;
    width:100%;
    height:55px;
    border:none;
    border-radius:5px;
    cursor:pointer;
    transition:color 0.4s linear;
    position:relative;
    overflow:hidden;
    z-index:1
}
.send-btn::before{
    border:2px solid transparent;
    content:"";
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:#e6373d;
    color:#ff2020;
    z-index:-1;
    transition:transform 0.5s;
    transition-timing-function:ease;
    transform-origin:0 0;
    transition-timing-function:cubic-bezier(0.5, 1.6, 0.4, 0.7);
    transform:scaleX(0)
}
.send-btn:hover::before{
    transform:scaleY(1);
    order:2px solid transparent;
    color:red
}
